* {
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
span.App-name {
  font-size: 3em;
}
span.App-date {
  font-size: 1.5em;
}

nav > ul >li {
  display: inline;
  list-style-type: none;
  margin: 0 5px;
  background: rgb(11, 19, 19);
  padding: 21px;
  font-size: 20px;
}

nav > ul >li > a {
  text-decoration: none;
  color: white;
}

nav > ul >li:hover {
  background-color: brown;
  cursor: pointer;
}

.form-item {
  margin: 5px;
}

.form-overtime {
  width: 600px;
  margin: auto;
  background: #5d7f9d;
  color: white;
  font-size: 25px;
  padding: 8px;
}

.form-input > input {
  padding: 8px;
  font-size: 15px;
  width: 100%;
}

button.form-submit {
  font-size: 25px;
  padding: 12px;
  margin: 2px;
  width: 150px;
  border: none;
  background: dimgrey;
  color: white;
  cursor: pointer;
}

.form-input > select {
  width: 100%;
  padding: 6px;
  font-size: 15px;
}

table {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
  margin-top: 10px;
}

table td,table th {
  border: 1px solid #ddd;
  padding: 8px;
}

table tr:nth-child(even){background-color: #f2f2f2;}

table tr:hover {background-color: #ddd;}

table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #04AA6D;
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
